<template>
<div>
    <div class="bkground">
        <section class="head searchHead clearfix">
            <div class="backWrap">
                <a @click="goBack" class="iconfont back"> &#xe614; </a>
            </div>
            <div class="searchHeadRight">
                <div class="chartWrap">   <a href="javascript:void(0)" @click="$router.push('cart')" class="iconfont cart"> &#xe669; </a></div>
            </div>
        </section>
        <div class="photoWrap">
            <div class="photo">
                <img class="cameras" src="../common/images/Camera.png">
            </div>
        </div>
        <div class="nameWrap">
            <span>{{user.userName}}</span>
            <img v-if="user.headImage" class="edit" :src="user.headImage">
            <img v-if="!user.headImage" class="edit" src="../common/images/edit.png">
        </div>
        <span class="email">{{user.email}}</span>
    </div>
    <div>
        <ul>
            <li @click="$router.push('orderList')">
                <div class="iconWrap">
                    <img src="../common/images/myAddress.png">
                </div>
                <div class="title">
                    My Orders
                </div>
            </li>
            <li @click="$router.push('whislist')">
                <div class="iconWrap">
                    <img src="../common/images/wish.png">
                </div>
                <div class="title">
                    Whislist
                </div>
            </li>
            <li @click="$router.push('coupons')">
                <div class="iconWrap">
                    <img src="../common/images/Mycoupons.png">
                </div>
                <div class="title">
                    My Coupons
                </div>
            </li>
            <li @click="$router.push('myAddress')">
                <div class="iconWrap">
                    <img src="../common/images/wodedizhi.png">
                </div>
                <div class="title">
                    My Address
                </div>
            </li>
            <li @click="$router.push('myBankcards')">
                <div class="iconWrap">
                    <img src="../common/images/MyBankcard.png">
                </div>
                <div class="title">
                    My Bank card
                </div>
            </li>
            <li @click="$router.push('feedBack')">
                <div class="iconWrap">
                    <img src="../common/images/FAQandContactus.png">
                </div>
                <div class="title">
                    FAQ and Contact us
                </div>
            </li>
            <li @click="$router.push('')">
                <div class="iconWrap">
                    <img src="../common/images/ShareUs.png">
                </div>
                <div class="title">
                    Share Us
                </div>
            </li>
            <li @click="confirm">
                <div class="iconWrap">
                    <img src="../common/images/LogOut.png">
                </div>
                <div class="title">
                    Log Out
                </div>
            </li>
        </ul>
        <div class="foter">
            <h6>1.0.0</h6>
            <div class="notice">
                <div class="Privacy">
                    Privacy Notice
                </div>
                <div class="terms">
                    Terms and Conditions
                </div>
            </div>
            <div class="logoWrap">
                <img src="../common/images/logo@2x.png">
            </div>
        </div>
    </div>

</div>
</template>

<script>
import{mapGetters} from 'vuex'
export default {
  name: 'center',
  data () {
    return {

    }
  },
  computed:{
      ...mapGetters(['user','indexData'])
  },
  methods: {
    init(){

    },
    confirm(){
      this.$dialog.confirm({
        message: 'Confirm to Log Out?',
        confirmButtonText:'confirm',
        cancelButtonText:'Cancel',
      }).then(() => {
          this.$store.dispatch('logout');
        // on confirm
      }).catch(() => {
        // on cancel
      });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style scoped>
    .bkground{
        background: url('../common/images/layer39.png') no-repeat;
        background-size: cover;
        width: 100%;
        height: 222px;
    }
    .backWrap{float: left; width:25px; line-height: 42px;}
    .searchHeadRight{width:35px; float: right; text-align: right;line-height: 42px;}
    .head{
        display: block;
    }
    .head .iconfont{
        color: white;
    }
    .photoWrap{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .photo{
        position: relative;
        width: 79px;
        height: 79px;
        border-radius: 50%;
        background: url('../common/images/defaultHead.png') no-repeat;
        background-size: cover;
    }
    .cameras{
        width: 25px;
        height: 25px;
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .edit{
        width: 18px;
        height: 18px;
    }
    .nameWrap{
        padding-top: 15px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .nameWrap span{
        color:  white;
        margin-right: 5px;
        font-size: 15px;
    }
    .email{
        color:  white;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 10px;
        font-size: 13px;
    }
    ul li{
        height: 53px;
        display: -webkit-box;
    }
    .iconWrap{
        width: 15%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .iconWrap img{
        width: 23px
    }
    .title{
        border-bottom: 1px solid #f2f3f5;
        height: 100%;
        display: flex;
        align-items: center;
        width: 85%;
        font-size: 15px;
        color:#333;
    }
    .foter{
        height: 157px;
        background-color: #F4F4F4;
        width: 100%;
    }
    h6{
       color: #666666;
       text-align: center;
       padding-top: 30px;
    }
    .Privacy{
        border-right: red solid 1px;
        width: 50%;
        padding-right: 26px;
        text-align: right;
        color: #666666;
    }
    .terms{
        width: 50%;
        padding-left: 21px;
        color: #666666;
    }
    .notice{
        display: -webkit-box;
        padding: 10px 0;
    }
    .logoWrap img{
        width: 47px;
        height: 47px;
    }
    .logoWrap {
        width: 100%;
        display: flex;
        justify-content: center;
        padding-top: 10px;
    }
</style>



